@import './reset.css';

:root {
    /* Colors */
    --blackpure: #000;
    --black: #171718;
    --primary-color: cornflowerblue;
    --black-border: #26292d;
    --white: #fff;
    --purple: #5c62ec;
    --dark-nav: #26345a;

    /* Nav */
    --nav-bg: var(--dark-nav);
    --nav-text: var(--white);

    /* Header */
    --header-bg: var(--primary-color);
    --header-text: var(--white);

    /* Footer */
    --footer-bg: var(--black);
    --footer-text: var(--white);

    /* Light mode */
    --page-bg: var(--primary-color);
    --text-color: var(--black);
    --accent: var(--white);
    --title-1: var(--accent);

    --project-card-bg: var(--white);
    --project-card-text: var(--black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.dark {
    /* Dark mode */
    --page-bg: var(--dark-nav);
    --text-color: var(--white);

    --title-1: var(--white);

    --project-card-bg: var(--black);
    --project-card-text: var(--white);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);
}


.dark header {

    animation: background-dark 5s ease-in-out;
}

@keyframes background-dark {
    0% {
        background-color: var(--dark-nav);
    }
    50% {
        background-color: black;
    }
    100% {
        background-color: var(--dark-nav);
    }
}

.header {
    animation: background 5s ease-in-out;
}

@keyframes background {
    0% {
        background-color: var(--primary-color);
    }
    50% {
        background-color: rgb(208, 208, 241);
    }
    100% {
        background-color: var(--primary-color);
    }
}

html,
body {
    min-height: 1100px;
    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;
    /* Light mode */
    background-color: var(--page-bg);
    color: white;
}

#root {
    min-height: 100vh;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* General */

.container {
    margin: 0 auto;
    padding: 0 15px;
    width: 100%;
}

.logo {
    width: 120px;
}

.none {
    display: none !important;
}

.section {
    padding: 70px 0;
}

.title-1 {
    margin-bottom: 40px;
    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--title-1);
    text-align: center;
}

.title-2 {
    margin-bottom: 40px;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.3;
}
/* Scroll*/

.scroll-indicator {
    --mouse-width: 32px;
    --mouse-height: calc(2 * var(--mouse-width));
    --line-width: 3px;
    --animation-duration: 2.5s;
    position: relative;
    width: var(--mouse-width);
    height: var(--mouse-height);
    border: var(--line-width) solid white;
    border-radius: var(--mouse-width);
    opacity: 0.2;
    animation: mouse-move var(--animation-duration) ease infinite;}

    ::after {
        --dot-size: calc(3 * var(--line-width));
        --dot-size-expanded: calc(var(--mouse-height) / 3);
        --space-around: calc(var(--mouse-width) / 2 - var(--dot-size) / 2);
        content: "";
        position: absolute;
        left: var(--space-around);
        top: var(--space-around);
        width: var(--dot-size);
        height: var(--dot-size);
        opacity: 0;
        background-color: white;
        border-radius: var(--dot-size);
        animation: dot-scroll var(--animation-duration) ease-out infinite;
    }

@keyframes mouse-move {
    25% {
        transform: translateY(10%);
        opacity: 0.5;
    }
    50% {
        opacity: 0.2;
    }
}

@keyframes dot-scroll {
    20% {
        opacity: 1;
    }
    25% {
        transform: translateY(0);
        height: var(--dot-size-expanded);
        animation-timing-function: ease;
    }
    35% {
        transform: translateY(calc(var(--dot-size-expanded) - var(--dot-size)));
        height: var(--dot-size);
    }
    50% {
        transform: translateY(calc(var(--dot-size-expanded) - var(--dot-size)));
        opacity: 0;
        height: var(--dot-size);
    }
}

/* Btn */

.btn {
    display: inline-block;
    height: 48px;
    padding: 12px 28px;

    border-radius: 5px;
    background-color: var(--primary-color);
    color: var(--white);
    font-size: 16px;
    font-weight: 500;

    transition: opacity 0.2s ease-in;
}

.btn:hover {
    opacity: 0.8;
}

.btn:active {
    position: relative;
    top: 1px;
}

/* Btn outline */

.btn-outline {
    display: flex;
    column-gap: 10px;
    align-items: center;

    height: 48px;
    padding: 12px 20px;

    border-radius: 5px;
    border: 1px solid #000;
    background-color: #fff;
    color: #000;

    transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
    opacity: 0.8;
}

.btn-outline:active {
    position: relative;
    top: 1px;
}

/* Projects  */

.projects {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;
}


/* Project details */

.project-details {
    margin: 0 auto;
    max-width: 865px;

    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
}

.project-details-cover {
    max-width: 100%;
    margin-bottom: 40px;

    box-shadow: var(--box-shadow);
    border-radius: 10px;
}

.project-details-desc {
    margin-bottom: 30px;

    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
}

/* Skills list */

.skills-list {
    margin: 0 auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 40px;
    text-align: center;
}

.skills-list a {
    color: var(--accent);
    display: grid;
    place-items: center;
}

.skills-list-item {
    width: 50%;
    font-size: 18px;
    line-height: 1.5;
    display: grid;
    grid-template-columns: 200px 200px 200px;
}

.skills-list-item p + p {
    margin-top: 0.5em;
}


@media (max-width: 620px) {
    /* Header  */
    .header {
        min-height: unset;
    }

    .header-title {
        color: white;
        font-size: 30px;
    }

    .header-title strong {
        font-size: 40px;
    }

    /* Nav */
    .nav-row {
        justify-content: space-between;
    }

    .dark-mode-btn {
        order: 0;
    }

    /* General */
    .section {
        padding: 40px 0;
    }

    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 30px;
    }

    /* Projects */
    .project-title {
        font-size: 22px;
    }

    /* Project page */
    .project-details-desc {
        margin-bottom: 20px;
        font-size: 22px;
    }

    /* Content List */
    .content-list {
        row-gap: 20px;
    }

    .content-list-item {
        font-size: 16px;
    }

    /* Footer */
    .footer {
        padding: 40px 0 30px;
    }

    .footer-wrapper {
        row-gap: 20px;
    }

    /* Social */
    .social {
        column-gap: 20px;
    }

    .social-item {
        width: 28px;
    }
}

